import { Component, OnInit } from '@angular/core';

import { Platform, NavController, LoadingController, AlertController, ToastController } from '@ionic/angular';
import { WitePrompt, AppMin } from "../core";


import { Storage } from '@ionic/storage';

import { Router } from '@angular/router';



interface orderList {
  toUserId: any,
  name: any,
  toUserImg: any,
  orderId: any,
  buyDate: any,
  orderState: any,
  orderStateTxt: any,
  serviceName: any,
  orderTxt: any,
  serviceId: any,
}

@Component({
  selector: 'app-obj-order-list',
  templateUrl: './obj-order-list.page.html',
  styleUrls: ['./obj-order-list.page.scss'],
})


export class ObjOrderListPage implements OnInit {
  pet: string = "jinxingzhong";
  loader;

  orderList_jinxingzhong: orderList[] = new Array();
  orderList_daipingjia: orderList[] = new Array();
  orderList_yiwancheng: orderList[] = new Array();

  isadd_orderList_jinxingzhong: boolean = true;
  isadd_orderList_daipingjia: boolean = true;
  isadd_orderList_yiwancheng: boolean = true;

  infiniteScroll;

  jinxingzhong_page = 0;
  daipingjia_page = 0;
  yiwancheng_page = 0;
  refresher;
  isud: boolean = false;

  header_class = "";
  row1_order;//导航栏;
  notMoer1: boolean = true;
  notMoer2: boolean = true;
  notMoer3: boolean = true;
  constructor(public navCtrl: NavController, public Router: Router, public storage: Storage, public platform: Platform, private toastCtrl: ToastController, public loadingCtrl?: LoadingController, public appMin?: AppMin, private alertCtrl?: AlertController) {
    this.row1_order = document.getElementById("row1_order");

    let this1 = this;
    if (appMin.GetPhoneMode() == "ios") {
      this.header_class = "header_class";
    }
    else {
      this.header_class = "header_class_md";
      console.log("判断为安卓系统 设置状态栏样式")
    }




    platform.ready().then(() => {
      document.addEventListener('backbutton', function (evt) {
        if (cordova.platformId !== 'windows') {
          return;
        }
        alert('backbutton事件触发');
        window.history.back();

      }, false);

    });
  }

  ionViewDidLoad() {
  
  }
  segmentChanged(event) {
    this.addObjOrderList();
  }
  send(value, isshow?: boolean) {
    if (isshow) {
      this.loader = this.loadingCtrl.create({
        message: "加载中...",
        duration: 3000
      });
      this.loader.present();
    }



    this.appMin.post(JSON.parse(value)).subscribe((data) => {
      this.dataHandle(data, true);
    })


  }
  ionViewDidEnter() {

    document.querySelector('#ion-segment-button1-obj').shadowRoot.querySelector('.button-native').querySelector('ion-ripple-effect').shadowRoot.querySelector('style').innerHTML = ''
    document.querySelector('#ion-segment-button2-obj').shadowRoot.querySelector('.button-native').querySelector('ion-ripple-effect').shadowRoot.querySelector('style').innerHTML = ''
    document.querySelector('#ion-segment-button3-obj').shadowRoot.querySelector('.button-native').querySelector('ion-ripple-effect').shadowRoot.querySelector('style').innerHTML = ''


  }


  addObjOrderList() {
    //加载我的订单列表
    if (this.pet == "jinxingzhong") {
      WitePrompt("当前pet=jinxingzhong");
      WitePrompt("当前jinxingzhong_page=" + this.jinxingzhong_page);
      if (this.jinxingzhong_page == 0) {

      } else {
        return;
      }
    }
    else if (this.pet == "daipingjia") {
      WitePrompt("当前pet=daipingjia");
      WitePrompt("当前daipingjia_page=" + this.daipingjia_page);
      if (this.daipingjia_page == 0) {

      } else {
        return;
      }
    }
    else {
      WitePrompt("当前pet=" + this.pet);
      WitePrompt("当前yiwancheng_page_page=" + this.yiwancheng_page);
      if (this.yiwancheng_page == 0) {

      } else {
        return;
      }
    }

    let obj = {
      type: 'addObjOrderList',
      userId: this.appMin.getUserid(),
      page: 0,
      selectType: this.pet

    }
    this.send(JSON.stringify(obj), false);

  }
  dataHandle(meg: any, isSave: boolean) {
    // WitePrompt(JSON.stringify(meg));
    if (this.refresher) {
      this.refresher.target.complete();
    }
    if (meg["type"] == null)
      return;

    if (meg['type'] == 'addObjOrderList_ret') {
      //加载初始数据
      //console.log("addObjOrderList_ret="+JSON.stringify(meg));


      let count = meg['count'];
      let selectType = meg['selectType'];
      let page = meg['page'];
      WitePrompt('selectType=' + selectType);
      if (selectType == "jinxingzhong") {
        this.jinxingzhong_page = page;
        if (page - 10 == 0) {
          WitePrompt("首次加载")
          this.orderList_jinxingzhong = meg['rows'];
        } else {
          for (let i = 0; i < count; i++) {
            this.orderList_jinxingzhong.push(meg['rows'][i]);
          }
        }
        if (this.orderList_jinxingzhong.length != 0) {
          this.notMoer1 = false;
        }

        if (count < 10) {
          this.isadd_orderList_jinxingzhong = false;
        }

        //判断是否显示"没有更多数据了"




      } else if (selectType == "daipingjia") {
        this.daipingjia_page = page;
        if (page - 10 == 0) {
          WitePrompt("首次加载")
          this.orderList_daipingjia = meg['rows'];
        } else {
          for (let i = 0; i < count; i++) {
            this.orderList_daipingjia.push(meg['rows'][i]);
          }
        }
        if (this.orderList_daipingjia.length != 0) {
          this.notMoer2 = false;
        }

        if (count < 10) {
          this.isadd_orderList_daipingjia = false;
        }

      } else {
        this.yiwancheng_page = page;
        if (page - 10 == 0) {
          WitePrompt("首次加载")
          this.orderList_yiwancheng = meg['rows'];
        } else {
          for (let i = 0; i < count; i++) {
            this.orderList_yiwancheng.push(meg['rows'][i]);
          }
        }
        if (this.orderList_yiwancheng.length != 0) {
          this.notMoer3 = false;
        }

        if (count < 10) {
          this.isadd_orderList_yiwancheng = false;
        }

      }

      if (meg['count'] == 0) {
        //没有数据
      }
      WitePrompt("addObjOrderList_ret=" + JSON.stringify(meg));


      // document.getElementById("row1_order").style.height = "45px";
      if (isSave) {
        this.storage.set("addObjOrderList_ret", meg);

      }

    }
    if (meg['type'] == 'addObjOrderServiceTxt_ret') {
      // WitePrompt("addOrderServiceTxt_ret=" + JSON.stringify(meg));
      // let alert =await this.alertCtrl.create({
      //   message: "说明：" + meg['serviceTxt'],
      //   subHeader: meg['serviceName'],
      //   buttons: ['确定']
      // });
      // alert.present();
      this.showalert(meg['serviceTxt'], meg['serviceName'], '确定')

    }

  }
  async showalert(msg, header, button) {
    let alert = await this.alertCtrl.create({
      message: "说明：" + msg,
      subHeader: header,
      buttons: [button]
    });
    alert.present();
  }

  alertServiceTxt(serviceId) {
    //显示服务名称和内容
    //alert(serviceId);
    let obj = {
      type: 'addObjOrderServiceTxt',
      serviceId: serviceId
    }
    this.send(JSON.stringify(obj));

  }
  toDetails(orderId, toUserId) {
    //详情按钮
    //跳转到详情页
    let obj = {
      orderId: orderId,


    }
    // this.navCtrl.push(ObjOrderListShowPage, obj);
    this.Router.navigate(['obj-order-list-show'], { queryParams: obj });
  }

  doInfinite(infiniteScroll) {
    //上拉刷新
    // infiniteScroll.complete();


 console.log(infiniteScroll);



    let page;
    let pet = this.pet;
    if (pet == 'jinxingzhong') {
      page = this.jinxingzhong_page;
      //判断是否还有数据可加载
      if (this.isadd_orderList_jinxingzhong == false) {
        infiniteScroll.target.complete();
        return;
      }
     
    } else if (pet == 'daipingjia') {
      page = this.daipingjia_page;
      if (this.isadd_orderList_daipingjia == false) {
        infiniteScroll.target.complete();
        return;
      }
    }
    else if (pet == 'yiwancheng') {
      page = this.yiwancheng_page;
      if (this.isadd_orderList_yiwancheng == false) {
        infiniteScroll.target.complete();
        return;
      }
    }

    let obj = {
      type: 'addObjOrderList',
      userId: this.appMin.getUserid(),
      page: page,
      selectType: this.pet

    }
    this.send(JSON.stringify(obj), false);

    setTimeout(() => {
      if(infiniteScroll)
      infiniteScroll.target.complete();

    }, 2000);


  }
  doRefresh(refresher) {
    //下拉刷新
    this.refresher = refresher;

    console.log('下滑刷新', refresher);
    let obj = {
      type: 'addObjOrderList',
      userId: this.appMin.getUserid(),
      page: 0,
      selectType: this.pet
    }
    this.send(JSON.stringify(obj), false);


  }
  toConsultation(orderId, toUserId) {
    //咨询咨询按钮

    let obj = {
      orderId: orderId,
      toUserId: toUserId,
      userisobj: '1'
    }

    //this.navCtrl.push(ConsultTimePage, obj);

    this.Router.navigate(["consult-time"], { queryParams: obj })

  }

  ngOnInit() {
    this.addObjOrderList();//加载初始数据
    console.log('ngOnInit ');

  
  }

}
